<style scoped>
   a {
    color:#000000;
  }

</style>
<template>
  <div class="uploadalg">

<el-row>
  <el-col :span="6"><div class="grid-content" @click="changenewshow()" style="text-align:left">
      
          <i class="el-icon-circle-plus" v-show="!newshow">
          </i>
          <i class="el-icon-remove" v-show="newshow">
        </i> <a></a>
    </div></el-col>
  <el-col :span="18"><div class="grid-content" ></div></el-col>
</el-row>
  <div v-show="newshow">
   <el-row :gutter="20">
    <el-col :span="12">
     <el-row>
       <el-col :span="12">
         
       </el-col>
       <el-col :span="12">
         <div @change='changename'>
            <input type="file"  class="pull-left btn btn-warning" id="alg_jar" name="alg_jar" accept=".jar" >
         </div>
       </el-col>
       <br/>
        <br/> <br/> <br/> <br/> <br/>
      <el-col :span="12">
         
       </el-col>
       <el-col :span="12">

       <el-input
        placeholder=""
        v-model="filename"
        clearable>
        </el-input>

       </el-col>
     </el-row>
    </el-col>
    <el-col :span="12">
         <div class="form-group">
                  <label for="username" class="control-label"></label>
                  <div>
                    <p align="left" style="color:gray">
                    </p>
                    <p align="left" style="color:gray"> jar
                    </p>
                    <p align="left" style="color:gray">
                    </p>
                    <p align="left" style="color:gray">
                    </p>
                    <p align="left" style="color:gray">
                    </p>
                    <p align="left" style="color:gray">flag:10<br/>
                    </p>

                  </div>
                </div>
    </el-col>
         <el-button @click="upload" class="btn btn-success" ></el-button>
          <el-button @click='clear' class="btn btn-warning"></el-button>
         <el-button type="info"></el-button>
   </el-row>
  </div>
</div>
</template>
<script>

  export default {
    data() {
      return {
       newshow:false,
      filename: '', //
      jarname:'',
      }
    },
    computed: {
   
  },
    created: function () {
      var vm = this;
    

     


    }, 
    methods: {
      changenewshow(){
      var vm = this;
      vm.newshow = !vm.newshow;

       },
       clear: function() {
        var vm = this;
        vm.filename = '';
        vm.a=false;
        var obj = document.getElementById('alg_jar');
        obj.outerHTML = obj.outerHTML;
      },
      changename: function() {
        var vm = this;
        vm.a =true;
        var path = $("#alg_jar").val();
        var pathstr = path.split("\\");
        vm.filename = pathstr[pathstr.length - 1];
      },
       upload: function(id) {
        var vm = this;
//        var url = vm.url + "api/algorithms/upload/";
        var url = "http://localhost:8080/dataclean/servlet/DataCleanServlet";
        var formData = new FormData();
        formData.append("jar", document.getElementById("alg_jar").files[0]);
        vm.jarname=document.getElementById("alg_jar").files[0].name;
        var path = $("#alg_jar").val();
        var pathstr = path.split("\\");
        var filetypes =[".jar"];
        var fileend = vm.filename.substring(vm.filename.indexOf("."));
        var isnext = false;
        var filenoend = vm.filename.substring(0,vm.filename.indexOf("."))
        vm.a=true;
        if (path == '') {
          alert("");
          return;
        }
        console.log("filename:" + vm.filename);
        if (vm.filename == '') {
          alert("");
          return;
        }

        var testjar=pathstr[pathstr.length-1].split('.');
        // if(testjar[testjar.length-1]!="jar"){
        //   alert("jar");
        //   return;
        // }
        if (/^[\u4e00-\u9fa5]+$/i.test(filenoend)) {

          alert("");
          return;
        }
        formData.append("name", vm.filename);
        formData.append("userid", vm.userid);
        //   console.log("path"+path);
        //              $.ajax({
        //                  url: url,
        //                  type: 'POST',
        //                  cache: false,
        //                  data: formData,
        //                  processData: false,
        //                  contentType: false,
        //                  success: function(data) {
        //
        //                  },
        //                  error: function(data) {}
        //              });
        //

        $.ajax({
          url: url,
          type: 'POST',
          data: formData,
          /**
           * falsejQuery formdata 
           * XMLHttpRequest formdata 
           */
          processData: false,
          /**
           *falseContent-Type
           */
          contentType: false,
          dataType: 'json',

          success: function(data) {
            //data jsonjson
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            //    console.log(Obj);

            if (Obj.status == 0) {
              // console.log(data);
              alert("");
              vm.clear();
            } else {
              alert("" + ":" + Obj.info);
             
            }
            vm.clear();
          },
          error: function(data) {
          //  alert("");
          alert("");
               vm.submitjar();
            console.log(data); //jsonjson
            vm.clear();
          }
        });
      },
      submitjar(){
           var vm = this;
        var url = "http://localhost:8080/dataclean/servlet/addjar";
  
         var data_tmp = {
           "jarfile":vm.jarname,
           "jarname":vm.filename,
          };
           $.ajax({
          url: url,
          type: "POST",
          async:false,
          dataType: 'json',
          data: {
            'data_json': JSON.stringify(data_tmp)
          },
           success: function (data) {
            //data jsonjson
//                            var Obj = eval("(" + data + ")");
            var Obj = data;
            vm.fresh();
          },
           error: function (data) {
            console.log(data);
          }
        });
      },
       fresh() {
      this.$emit('fresh'); //upup'hehe'
    },
    }
  }
</script>



